/* Pawsera Mobile App Styles */

/* CSS Variables for consistent theming */
:root {
  --primary-orange: #F7931E;
  --background-light: #F8F8F8;
  --text-dark: #212121;
  --text-gray: #757575;
  --text-light-gray: #9CA3AF;
  --border-light: #E0E0E0;
  --white: #FFFFFF;
  --error-red: #E53E3E;
  --success-green: #38A169;
  --warning-yellow: #F59E0B;
  --shadow-light: 0 2px 8px rgba(0, 0, 0, 0.1);
  --shadow-medium: 0 4px 12px rgba(0, 0, 0, 0.15);
  --shadow-large: 0 8px 24px rgba(0, 0, 0, 0.2);
  --border-radius: 8px;
  --border-radius-large: 12px;
  --border-radius-xl: 16px;
  --transition: all 0.2s ease-in-out;
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
}

/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  background-color: var(--background-light);
  color: var(--text-dark);
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Main container - moved to shared styles section */

.login-content {
  width: 100%;
  max-width: 375px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

/* Brand section */
.brand-section {
  margin-bottom: 32px;
  text-align: center;
}

.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin-bottom: 16px;
}

.paw-logo {
  width: 32px;
  height: 32px;
  color: var(--primary-orange);
  fill: currentColor;
}

.brand-name {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-dark);
  letter-spacing: -0.5px;
  margin: 0;
}

.welcome-text {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-dark);
  margin: 0;
}

/* Form container */
.login-form {
  width: 100%;
}

.auth-form {
  width: 100%;
}

.form-container {
  background-color: var(--white);
  border-radius: var(--border-radius);
  padding: 24px;
  box-shadow: var(--shadow-light);
  margin-bottom: 24px;
}

/* Input groups */
.input-group {
  margin-bottom: 16px;
}

.input-group:last-of-type {
  margin-bottom: 0;
}

.form-input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid var(--border-light);
  border-radius: var(--border-radius);
  font-size: 16px;
  color: var(--text-dark);
  background-color: var(--white);
  transition: var(--transition);
  outline: none;
  box-sizing: border-box;
}

.form-input::placeholder {
  color: var(--text-gray);
  font-size: 16px;
}

.form-input:focus {
  border-color: var(--primary-orange);
  box-shadow: 0 0 0 3px rgba(247, 147, 30, 0.1);
}

.form-input.error {
  border-color: var(--error-red);
  box-shadow: 0 0 0 3px rgba(229, 62, 62, 0.1);
}

/* Password container */
.password-container {
  position: relative;
  display: flex;
  align-items: center;
}

.password-input {
  padding-right: 48px;
}

.password-toggle {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 4px;
  color: var(--text-gray);
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
}

.password-toggle:hover {
  color: var(--primary-orange);
  background-color: rgba(247, 147, 30, 0.1);
}

.password-toggle:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
}

.eye-icon {
  width: 20px;
  height: 20px;
  fill: currentColor;
}

/* Error messages */
.error-message {
  color: var(--error-red);
  font-size: 14px;
  margin-top: 4px;
  text-align: left;
  font-weight: 500;
}

/* Forgot password */
.forgot-password-container {
  text-align: right;
  margin: 16px 0 24px 0;
}

.forgot-password-link {
  color: var(--primary-orange);
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  transition: var(--transition);
}

.forgot-password-link:hover {
  color: #E67E22;
  text-decoration: underline;
}

.forgot-password-link:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
  border-radius: 2px;
}

/* Login button */
.login-button {
  width: 100%;
  padding: 14px 24px;
  background-color: var(--primary-orange);
  color: var(--white);
  border: none;
  border-radius: var(--border-radius);
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  outline: none;
  box-sizing: border-box;
}

.login-button:hover:not(.disabled) {
  background-color: #E67E22;
  transform: translateY(-1px);
  box-shadow: var(--shadow-medium);
}

.login-button:active:not(.disabled) {
  transform: translateY(0);
  box-shadow: var(--shadow-light);
}

.login-button:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
}

.login-button.disabled {
  background-color: var(--text-gray);
  cursor: not-allowed;
  opacity: 0.6;
  transform: none;
  box-shadow: none;
}

.auth-button {
  width: 100%;
  padding: 14px 24px;
  background-color: var(--primary-orange);
  color: var(--white);
  border: none;
  border-radius: var(--border-radius);
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  outline: none;
}

.auth-button:hover:not(.disabled) {
  background-color: #E67E22;
  transform: translateY(-1px);
  box-shadow: var(--shadow-medium);
}

.auth-button:active:not(.disabled) {
  transform: translateY(0);
  box-shadow: var(--shadow-light);
}

.auth-button:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
}

.auth-button.disabled {
  background-color: var(--text-gray);
  cursor: not-allowed;
  opacity: 0.6;
  transform: none;
  box-shadow: none;
}

/* Register section */
.register-section {
  text-align: center;
}

.auth-link-section {
  text-align: center;
}

.register-text {
  color: var(--text-dark);
  font-size: 14px;
  margin: 0;
}

.register-link {
  color: var(--primary-orange);
  text-decoration: none;
  font-weight: 600;
  transition: var(--transition);
}

.register-link:hover {
  color: #E67E22;
  text-decoration: underline;
}

.register-link:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
  border-radius: 2px;
}

.auth-link {
  color: var(--primary-orange);
  text-decoration: none;
  font-weight: 600;
  background: none;
  border: none;
  cursor: pointer;
  font-size: inherit;
  transition: var(--transition);
}

.auth-link:hover {
  color: #E67E22;
  text-decoration: underline;
}

.auth-link:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
  border-radius: 2px;
}

.auth-link-text {
  color: var(--text-dark);
  font-size: 14px;
  margin: 0;
}

/* Terms and Conditions */
.terms-section {
  margin: 16px 0 24px 0;
}

.terms-checkbox {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  cursor: pointer;
}

.terms-input {
  margin: 0;
  width: 16px;
  height: 16px;
  flex-shrink: 0;
  margin-top: 2px;
}

.terms-text {
  font-size: 14px;
  color: var(--text-dark);
  line-height: 1.4;
}

.terms-link {
  color: var(--primary-orange);
  text-decoration: none;
  font-weight: 500;
}

.terms-link:hover {
  text-decoration: underline;
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus visible for better keyboard navigation */
.form-input:focus-visible,
.password-toggle:focus-visible,
.login-button:focus-visible,
.forgot-password-link:focus-visible,
.register-link:focus-visible {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  :root {
    --border-light: #000000;
    --text-gray: #000000;
  }
  
  .form-input {
    border-width: 2px;
  }
  
  .form-input:focus {
    border-width: 3px;
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  :root {
    --background-light: #1A1A1A;
    --text-dark: #FFFFFF;
    --text-gray: #B0B0B0;
    --border-light: #404040;
    --white: #2A2A2A;
  }
  
  .form-container {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  }
}

/* Mobile-specific optimizations */
@media (max-width: 480px) {
  .login-container {
    padding: 16px;
  }
  
  .form-container {
    padding: 20px;
  }
  
  .brand-name {
    font-size: 22px;
  }
  
  .welcome-text {
    font-size: 18px;
  }
}

/* Very small screens */
@media (max-width: 320px) {
  .login-container {
    padding: 12px;
  }
  
  .form-container {
    padding: 16px;
  }
  
  .form-input {
    font-size: 16px; /* Prevents zoom on iOS */
  }
}

/* Landscape orientation adjustments */
@media (max-height: 500px) and (orientation: landscape) {
  .login-container {
    padding: 10px;
  }
  
  .brand-section {
    margin-bottom: 20px;
  }
  
  .form-container {
    padding: 16px;
  }
}

/* ===== SHARED SCREEN STYLES ===== */

/* Screen container for all pages (except login) */
.screen-container {
  min-height: 100vh;
  background-color: var(--background-light);
  padding: 0;
}

/* Override for login screen to use specific login styling */
.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: var(--background-light);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
}

.screen-content {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header section for all screens */
.header-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--spacing-md);
  background-color: var(--white);
  border-bottom: 1px solid var(--border-light);
  position: sticky;
  top: 0;
  z-index: 10;
}

.back-button, .logout-button {
  background: none;
  border: none;
  color: var(--primary-orange);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  padding: var(--spacing-sm);
  border-radius: var(--border-radius);
  transition: var(--transition);
}

.back-button:hover, .logout-button:hover {
  background-color: rgba(247, 147, 30, 0.1);
}

.back-button:focus, .logout-button:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
}

/* Page headers */
.page-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--spacing-lg) var(--spacing-md);
  background-color: var(--white);
}

.page-title {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-dark);
  margin: 0;
}

.add-button, .filter-button {
  background-color: var(--primary-orange);
  color: var(--white);
  border: none;
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--border-radius);
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
}

.add-button:hover, .filter-button:hover {
  background-color: #E67E22;
  transform: translateY(-1px);
}

.add-button:focus, .filter-button:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
}

/* ===== HOME SCREEN STYLES ===== */

.welcome-section {
  text-align: center;
  padding: var(--spacing-xl) var(--spacing-md);
  background-color: var(--white);
  margin-bottom: var(--spacing-md);
}

.welcome-title {
  font-size: 28px;
  font-weight: 700;
  color: var(--text-dark);
  margin-bottom: var(--spacing-sm);
}

.welcome-subtitle {
  font-size: 16px;
  color: var(--text-gray);
  margin: 0;
}

.navigation-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-md);
  padding: 0 var(--spacing-md) var(--spacing-xl);
}

.nav-card {
  background-color: var(--white);
  border: 1px solid var(--border-light);
  border-radius: var(--border-radius-large);
  padding: var(--spacing-lg);
  text-align: center;
  cursor: pointer;
  transition: var(--transition);
  text-decoration: none;
  color: inherit;
}

.nav-card:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-medium);
  border-color: var(--primary-orange);
}

.nav-card:focus {
  outline: 2px solid var(--primary-orange);
  outline-offset: 2px;
}

.nav-icon {
  width: 48px;
  height: 48px;
  margin: 0 auto var(--spacing-md);
  color: var(--primary-orange);
}

.nav-icon svg {
  width: 100%;
  height: 100%;
}

.nav-title {
  font-size: 16px;
  font-weight: 600;
  color: var(--text-dark);
  margin-bottom: var(--spacing-xs);
}

.nav-description {
  font-size: 14px;
  color: var(--text-gray);
  margin: 0;
}

/* ===== PETS SCREEN STYLES ===== */

.pets-list {
  padding: 0 var(--spacing-md) var(--spacing-xl);
}

.pet-card {
  background-color: var(--white);
  border-radius: var(--border-radius-large);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-md);
  box-shadow: var(--shadow-light);
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
}

.pet-image {
  flex-shrink: 0;
}

.pet-avatar {
  width: 60px;
  height: 60px;
  background-color: var(--primary-orange);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
}

.pet-avatar svg {
  width: 30px;
  height: 30px;
}

.pet-info {
  flex: 1;
}

.pet-name {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-dark);
  margin-bottom: var(--spacing-xs);
}

.pet-details {
  font-size: 14px;
  color: var(--text-gray);
  margin-bottom: var(--spacing-xs);
}

.pet-age, .pet-last-visit {
  font-size: 12px;
  color: var(--text-light-gray);
  margin: 0;
}

.pet-actions {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
}

.action-button {
  background-color: var(--white);
  border: 1px solid var(--border-light);
  color: var(--text-dark);
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius);
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
}

.action-button:hover {
  background-color: var(--primary-orange);
  color: var(--white);
  border-color: var(--primary-orange);
}

.action-button.secondary {
  background-color: var(--text-light-gray);
  color: var(--white);
  border-color: var(--text-light-gray);
}

.action-button.primary {
  background-color: var(--primary-orange);
  color: var(--white);
  border-color: var(--primary-orange);
}

/* ===== APPOINTMENTS SCREEN STYLES ===== */

.appointments-list {
  padding: 0 var(--spacing-md) var(--spacing-xl);
}

.appointment-card {
  background-color: var(--white);
  border-radius: var(--border-radius-large);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-md);
  box-shadow: var(--shadow-light);
}

.appointment-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
  margin-bottom: var(--spacing-md);
}

.appointment-date {
  text-align: center;
  flex-shrink: 0;
}

.date-day {
  display: block;
  font-size: 24px;
  font-weight: 700;
  color: var(--primary-orange);
}

.date-month {
  display: block;
  font-size: 12px;
  color: var(--text-gray);
  text-transform: uppercase;
}

.appointment-info {
  flex: 1;
}

.appointment-pet {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-dark);
  margin-bottom: var(--spacing-xs);
}

.appointment-vet {
  font-size: 14px;
  color: var(--text-gray);
  margin-bottom: var(--spacing-xs);
}

.appointment-time {
  font-size: 14px;
  color: var(--text-dark);
  font-weight: 500;
  margin: 0;
}

.appointment-status {
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius);
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.appointment-status.confirmed {
  background-color: var(--success-green);
  color: var(--white);
}

.appointment-status.pending {
  background-color: var(--warning-yellow);
  color: var(--white);
}

.appointment-details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.appointment-type {
  font-size: 14px;
  color: var(--text-gray);
  margin: 0;
}

.appointment-actions {
  display: flex;
  gap: var(--spacing-xs);
}

/* ===== NEARBY VETS SCREEN STYLES ===== */

.search-section {
  padding: var(--spacing-md);
  background-color: var(--white);
  border-bottom: 1px solid var(--border-light);
}

.search-container {
  position: relative;
  display: flex;
  align-items: center;
}

.search-icon {
  position: absolute;
  left: var(--spacing-md);
  width: 20px;
  height: 20px;
  color: var(--text-gray);
  z-index: 1;
}

.search-input {
  width: 100%;
  padding: var(--spacing-md) var(--spacing-md) var(--spacing-md) 48px;
  border: 1px solid var(--border-light);
  border-radius: var(--border-radius-large);
  font-size: 16px;
  background-color: var(--background-light);
  transition: var(--transition);
}

.search-input:focus {
  outline: none;
  border-color: var(--primary-orange);
  background-color: var(--white);
}

.vets-list {
  padding: 0 var(--spacing-md) var(--spacing-xl);
}

.vet-card {
  background-color: var(--white);
  border-radius: var(--border-radius-large);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-md);
  box-shadow: var(--shadow-light);
}

.vet-header {
  margin-bottom: var(--spacing-md);
}

.vet-name {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-dark);
  margin-bottom: var(--spacing-xs);
}

.vet-address {
  font-size: 14px;
  color: var(--text-gray);
  margin-bottom: var(--spacing-sm);
}

.vet-meta {
  display: flex;
  gap: var(--spacing-md);
}

.vet-distance {
  font-size: 14px;
  color: var(--primary-orange);
  font-weight: 500;
}

.vet-rating {
  font-size: 14px;
  color: var(--text-dark);
  font-weight: 500;
}

.vet-details {
  margin-bottom: var(--spacing-md);
}

.vet-hours, .vet-phone {
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--spacing-xs);
}

.hours-label, .phone-label {
  font-size: 14px;
  color: var(--text-gray);
  font-weight: 500;
}

.hours-text, .phone-link {
  font-size: 14px;
  color: var(--text-dark);
}

.phone-link {
  color: var(--primary-orange);
  text-decoration: none;
}

.phone-link:hover {
  text-decoration: underline;
}

.vet-services {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-xs);
  margin-top: var(--spacing-sm);
}

.service-tag {
  background-color: var(--background-light);
  color: var(--text-dark);
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius);
  font-size: 12px;
  font-weight: 500;
}

.vet-actions {
  display: flex;
  gap: var(--spacing-sm);
}

/* ===== SETTINGS SCREEN STYLES ===== */

.profile-section {
  display: flex;
  align-items: center;
  padding: var(--spacing-lg);
  background-color: var(--white);
  margin-bottom: var(--spacing-md);
  gap: var(--spacing-md);
}

.profile-avatar {
  width: 60px;
  height: 60px;
  background-color: var(--primary-orange);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  flex-shrink: 0;
}

.profile-avatar svg {
  width: 30px;
  height: 30px;
}

.profile-info {
  flex: 1;
}

.profile-name {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-dark);
  margin-bottom: var(--spacing-xs);
}

.profile-email {
  font-size: 14px;
  color: var(--text-gray);
  margin: 0;
}

.edit-profile-button {
  background-color: var(--primary-orange);
  color: var(--white);
  border: none;
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--border-radius);
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
}

.edit-profile-button:hover {
  background-color: #E67E22;
}

.settings-list {
  background-color: var(--white);
  margin-bottom: var(--spacing-md);
}

.settings-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--spacing-lg);
  border-bottom: 1px solid var(--border-light);
  cursor: pointer;
  transition: var(--transition);
}

.settings-item:hover {
  background-color: var(--background-light);
}

.settings-item:last-child {
  border-bottom: none;
}

.settings-content {
  display: flex;
  align-items: center;
  gap: var(--spacing-md);
  flex: 1;
}

.settings-icon {
  font-size: 24px;
  width: 32px;
  text-align: center;
}

.settings-text {
  flex: 1;
}

.settings-title {
  font-size: 16px;
  font-weight: 500;
  color: var(--text-dark);
  margin-bottom: var(--spacing-xs);
}

.settings-subtitle {
  font-size: 14px;
  color: var(--text-gray);
  margin: 0;
}

.settings-action {
  display: flex;
  align-items: center;
}

.toggle-switch {
  position: relative;
  width: 48px;
  height: 28px;
  background-color: var(--border-light);
  border-radius: 14px;
  border: none;
  cursor: pointer;
  transition: var(--transition);
}

.toggle-switch.active {
  background-color: var(--primary-orange);
}

.toggle-slider {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 24px;
  height: 24px;
  background-color: var(--white);
  border-radius: 50%;
  transition: var(--transition);
}

.toggle-switch.active .toggle-slider {
  transform: translateX(20px);
}

.chevron-icon {
  width: 20px;
  height: 20px;
  color: var(--text-gray);
}

.logout-section {
  padding: var(--spacing-md);
}

.logout-button-large {
  width: 100%;
  background-color: var(--error-red);
  color: var(--white);
  border: none;
  padding: var(--spacing-md);
  border-radius: var(--border-radius);
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-sm);
}

.logout-button-large:hover {
  background-color: #C53030;
  transform: translateY(-1px);
}

.logout-icon {
  width: 20px;
  height: 20px;
}

.version-info {
  text-align: center;
  padding: var(--spacing-lg);
}

.version-text {
  font-size: 12px;
  color: var(--text-light-gray);
  margin: 0;
}

/* ===== EMPTY STATES ===== */

.empty-state {
  text-align: center;
  padding: var(--spacing-xl) var(--spacing-md);
  background-color: var(--white);
  border-radius: var(--border-radius-large);
  margin: var(--spacing-md);
}

.empty-icon {
  width: 64px;
  height: 64px;
  margin: 0 auto var(--spacing-md);
  color: var(--text-light-gray);
}

.empty-icon svg {
  width: 100%;
  height: 100%;
}

.empty-title {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-dark);
  margin-bottom: var(--spacing-sm);
}

.empty-description {
  font-size: 14px;
  color: var(--text-gray);
  margin-bottom: var(--spacing-lg);
}

.primary-button {
  background-color: var(--primary-orange);
  color: var(--white);
  border: none;
  padding: var(--spacing-md) var(--spacing-lg);
  border-radius: var(--border-radius);
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
}

.primary-button:hover {
  background-color: #E67E22;
  transform: translateY(-1px);
}

/* ===== RESPONSIVE ADJUSTMENTS ===== */

@media (max-width: 320px) {
  .navigation-grid {
    grid-template-columns: 1fr;
  }
  
  .nav-card {
    padding: var(--spacing-md);
  }
  
  .pet-card, .appointment-card, .vet-card {
    padding: var(--spacing-md);
  }
  
  .settings-item {
    padding: var(--spacing-md);
  }
}
